"use client";

import { COUPON_CODES } from "@/sanity/lib/sales/couponCodes";
import { getActiveSaleByCouponCode } from "@/sanity/lib/sales/getActiveSaleByCouponCode";
import { motion, AnimatePresence } from "framer-motion";
import { useEffect, useState } from "react";

export default function BlackFridayBanner() {
  const [sale, setSale] = useState<null | {
    title: string;
    description: string;
    couponCode: string;
    discountAmount: number;
    isActive: boolean;
  }>(null);

  useEffect(() => {
    async function fetchSale() {
      const res = await getActiveSaleByCouponCode(COUPON_CODES.BFRIDAY);
      if (res?.isActive) setSale(res);
    }
    fetchSale();
  }, []);

  if (!sale) return null;

  return (
    <AnimatePresence>
      <motion.div
        key="black-friday-banner"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -20 }}
        transition={{ duration: 0.5, ease: "easeOut" }}
        className="bg-gradient-to-r from-black via-gray-900 to-gray-800 text-white px-6 py-6 mx-4 mt-3 rounded-2xl shadow-lg"
      >
        <div className="container mx-auto flex flex-col sm:flex-row items-start sm:items-center justify-between gap-6">
          <div className="flex-1">
            <h2 className="text-3xl sm:text-5xl font-extrabold mb-2 sm:mb-4">
              {sale.title ?? ""}
            </h2>
            <p className="text-lg sm:text-2xl font-semibold mb-4 sm:mb-6">
              {sale.description ?? ""}
            </p>
            <motion.div
              whileHover={{ scale: 1.05 }}
              transition={{ duration: 0.3 }}
              className="inline-flex flex-col sm:flex-row gap-2 sm:gap-4 bg-white text-black py-4 px-6 rounded-full shadow-md cursor-pointer"
            >
              <span className="font-bold text-base sm:text-xl">
                Use code:{" "}
                <span className="text-orange-500">{sale.couponCode ?? ""}</span>
              </span>
              <span className="font-bold text-base sm:text-xl">
                for {sale.discountAmount ?? 0}% OFF
              </span>
            </motion.div>
          </div>
        </div>
      </motion.div>
    </AnimatePresence>
  );
}
